:root{
    --Header-font: 'Montserrat', sans-serif;
    --font-global: 'Trocchi', sans-serif;
    --global-color: #424e50;
    --nav-color: #626464;
    --line-color:#979797;
    
}

*{
    font-family: var(--font-global);
}

h1, h2, h3, span{
    text-transform: uppercase;
    font-family: var(--Header-font);
}

body{
    margin: 0;
    background-color: #d7d7d7;
}
/* Header */
.nav {
    position: fixed !important;
    border: none;
    background: #ffff;
    margin-bottom: 0;
    width: 100%;
    height: auto;
    padding: 2.2rem 0 1.5rem 0;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    z-index: 3; /* 3 is chosen as main element may also need a raise in index*/
    /* gap: 0rem; */
}

.nav img{
    width:10%;
}
.navButtons {
    display: flex;
    /* gap: 0.3rem; */
}
/* styles buttons */
button#home {
    border-left: none;
}
button#ai {
    border-right: none;
}
button {
    border-right: 3px solid var(--line-color);
    border-top: none;
    border-bottom: none;
    border-left: none;
    padding: 0.5rem 3rem;
    background: #ffff;
    font-size: large;
    font-weight: 600;
    color: var(--nav-color);
    text-transform: uppercase;
}
button:hover{
    cursor: pointer;
}

/* styles the menu button dropdown */
.dropdown__menu {
    display: flex;
    position: absolute;
    overflow: auto;
    border: 1px solid var(--line-color);
    background-color: #ffffff;
    color: var(--nav-color);
    flex-direction: column;
    align-items: center;
    gap: 1em;
    padding: 0.7rem 0.9rem;
    font-size: larger;
    font-weight: 600;
}
.dropdown__menu a{
    text-transform: uppercase;
    text-decoration: none;
    display: block;
    border-bottom: 1px solid var(--nav-color);
}
.hide{
    display: none;
}

/* Main */

main {
    padding-top: 5rem;
}
.home-background {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}),url(${___CSS_LOADER_URL_REPLACEMENT_1___}) center bottom no-repeat;
    background-blend-mode: multiply;
    background-size: auto, 80% 100%;
    min-height: 79rem;
    text-align: center;
}
.home_message {
    color: var(--global-color);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 1vh 25vw 1vh 25vw;
}
.home_message h3 {
    
    text-transform: uppercase;
    font-size: 1.6rem;
    font-weight: 500;
}
.homeContainer {
    flex: 1 1 auto;
    padding: 1rem 10rem 1rem 10rem;
}
.homeContainer p {
    margin: 0;
    padding-bottom: 2rem;
    font-weight: 400;
    letter-spacing: 0.03em;
}
.sans {
    font-size: 0.9rem;
    font-weight: 700;
    letter-spacing: 2px;
}

.container {
    width: 1170px;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

.row {
    margin-right: -15px;
    margin-left: -15px;
}

.home-background img {
    width: 80%;
    height: 80%;
    margin-top: 25rem;
}


.beersImage {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}),url(${___CSS_LOADER_URL_REPLACEMENT_2___}) center bottom no-repeat;
    background-blend-mode: multiply;
    background-position-x: center;
    background-size: auto, 98%;
    text-align: center;
    /* min-height: 1100px; */
    /* padding-top: 10%; */
}

.alcohol{
    display: grid;
    grid-template-columns: repeat(2, 3fr);
    text-align: center;
    /* background: url(./assets/dotpattern.png) left top repeat; */
}
.beers {
    display: grid;
    grid-template-rows: repeat(1, 2fr) repeat(9, 1fr);
    background-color: rgba(0,0,0,0.6);
    color: white;
    margin: 5rem;
    border-radius: 2rem;
}
.seasonal, .cocktails {
    font-family: 'Protest Strike', sans-serif;
    transform: rotate(360deg);
    text-decoration: underline;
    font-size: 3rem;
    letter-spacing: 5px;
}
.drinks {
    display: grid;
    grid-template-rows: repeat(1, 2fr) repeat(10,1fr);
    background-color: rgba(0,0,0,0.6);
    color: white;
    margin: 5rem;
    border-radius: 2rem;
}


/* Dinner Menu */
.dinner{
    background: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
    padding-bottom: 6rem;
}
.dinnerText {
    display: flex;
    justify-content: center;
    align-items: center;
}
.dinner img{
    padding:5rem 0 5rem 0;
}
.dinnerMenu {
    display: grid;
    margin: 0rem 15rem 0rem 15rem;
    grid-template-columns: repeat(2, 1fr);
    gap: 15rem;
}
.dinner1, .dinner2 {
    background-color: whitesmoke;
    padding: 0 3rem 5rem 3rem;
    border-radius: 2rem;
}
/* grid properties for dinner menues */
.dinner1 {
    display: grid;
    grid-template-rows: repeat(6, auto);
}
.dinner2 {
    display: grid;
    grid-template-rows: repeat(4, auto);
}

.dinner h1, .breakfast h1{
    color: #799b5e;
    text-decoration: underline;
}


/* Breakfast Menu */
.breakfast{
    padding-bottom: 6rem;
}
.breakfastMenu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 15rem;
}

.breakfast img{
    padding: 5rem 0 5rem 0
}

.breakfastText{
    display: flex;
    justify-content: center;
    align-items: center;
}


.breakfast1, .breakfast2 {
    background-color: whitesmoke;
    border-radius: 2rem;
    padding: 0rem 1rem 0rem 1rem;
}
/* Events */
.events {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}), url(${___CSS_LOADER_URL_REPLACEMENT_4___}) center top no-repeat;
    background-blend-mode: multiply;
    min-height: 100vh;
    padding: 3rem 0 3rem 0;
    background-size: auto, 98% 100%;
}
.events img {
    padding: 1rem 0 5rem 0;
}
.eventBanner {
    display: flex;
    justify-content: center;
}
.highlights, .event_list{
    color:whitesmoke;
}

.event-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 4em;
}

.day {
    flex: 0 0 30%;
    flex-grow: 0;
    display: flex;
    flex-direction: column;
}

.highlights{
    background-color: #5a92ade3;
    font-weight: normal;
    text-transform: uppercase;
    font-size: 3rem;
    display: inline;
    text-align: center;
    letter-spacing: 2px;
}

.event_list {
    font-size: 2rem;
    background-color: rgba(0,0,0,0.5);
    border-radius: 0rem;
}

/* Footer  */

.site-footer{
    background-color: #AFCB98;
    padding: 1rem 0 1rem 0;
}

.footerContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}


.address{
    text-align: center;
    font-style: normal;
}
.direction{
    text-decoration: underline;
}
a{
    text-decoration: none;
}
a:hover, a:visited, a:active{
    color: inherit;
}


/* AI COMING SOON CSS-Delete later */
.aiMain {
    display: flex;
    padding: 25rem;
    justify-content: center;
    font-size: xxx-large;
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}), url(${___CSS_LOADER_URL_REPLACEMENT_5___}) center top no-repeat;
    background-size: auto, 50% 100%;
    background-color: rgba(0,0,0, 0.3);
}

h1.comingSoon {
    color: #000000;
    padding-top: 15rem;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAIA,qBAAqB;AACrB;IACI,uCAAuC;IACvC,oCAAoC;IACpC,uBAAuB;IACvB,oBAAoB;IACpB,oBAAoB;;AAExB;;AAEA;IACI,+BAA+B;AACnC;;AAEA;IACI,yBAAyB;IACzB,+BAA+B;AACnC;;AAEA;IACI,SAAS;IACT,yBAAyB;AAC7B;AACA,WAAW;AACX;IACI,0BAA0B;IAC1B,YAAY;IACZ,iBAAiB;IACjB,gBAAgB;IAChB,WAAW;IACX,YAAY;IACZ,0BAA0B;IAC1B,aAAa;IACb,6BAA6B;IAC7B,mBAAmB;IACnB,UAAU,EAAE,8DAA8D;IAC1E,eAAe;AACnB;;AAEA;IACI,SAAS;AACb;AACA;IACI,aAAa;IACb,iBAAiB;AACrB;AACA,mBAAmB;AACnB;IACI,iBAAiB;AACrB;AACA;IACI,kBAAkB;AACtB;AACA;IACI,yCAAyC;IACzC,gBAAgB;IAChB,mBAAmB;IACnB,iBAAiB;IACjB,oBAAoB;IACpB,iBAAiB;IACjB,gBAAgB;IAChB,gBAAgB;IAChB,uBAAuB;IACvB,yBAAyB;AAC7B;AACA;IACI,eAAe;AACnB;;AAEA,oCAAoC;AACpC;IACI,aAAa;IACb,kBAAkB;IAClB,cAAc;IACd,mCAAmC;IACnC,yBAAyB;IACzB,uBAAuB;IACvB,sBAAsB;IACtB,mBAAmB;IACnB,QAAQ;IACR,sBAAsB;IACtB,iBAAiB;IACjB,gBAAgB;AACpB;AACA;IACI,yBAAyB;IACzB,qBAAqB;IACrB,cAAc;IACd,yCAAyC;AAC7C;AACA;IACI,aAAa;AACjB;;AAEA,SAAS;;AAET;IACI,iBAAiB;AACrB;AACA;IACI,mHAA6F;IAC7F,+BAA+B;IAC/B,+BAA+B;IAC/B,iBAAiB;IACjB,kBAAkB;AACtB;AACA;IACI,0BAA0B;IAC1B,aAAa;IACb,eAAe;IACf,uBAAuB;IACvB,mBAAmB;IACnB,yBAAyB;AAC7B;AACA;;IAEI,yBAAyB;IACzB,iBAAiB;IACjB,gBAAgB;AACpB;AACA;IACI,cAAc;IACd,8BAA8B;AAClC;AACA;IACI,SAAS;IACT,oBAAoB;IACpB,gBAAgB;IAChB,sBAAsB;AAC1B;AACA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,kBAAkB;IAClB,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA;IACI,mBAAmB;IACnB,kBAAkB;AACtB;;AAEA;IACI,UAAU;IACV,WAAW;IACX,iBAAiB;AACrB;;;AAGA;IACI,mHAA2F;IAC3F,+BAA+B;IAC/B,6BAA6B;IAC7B,0BAA0B;IAC1B,kBAAkB;IAClB,wBAAwB;IACxB,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,qCAAqC;IACrC,kBAAkB;IAClB,8DAA8D;AAClE;AACA;IACI,aAAa;IACb,iDAAiD;IACjD,iCAAiC;IACjC,YAAY;IACZ,YAAY;IACZ,mBAAmB;AACvB;AACA;IACI,yCAAyC;IACzC,yBAAyB;IACzB,0BAA0B;IAC1B,eAAe;IACf,mBAAmB;AACvB;AACA;IACI,aAAa;IACb,iDAAiD;IACjD,iCAAiC;IACjC,YAAY;IACZ,YAAY;IACZ,mBAAmB;AACvB;;;AAGA,gBAAgB;AAChB;IACI,mDAAyC;IACzC,oBAAoB;AACxB;AACA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;AACA;IAC